import React, { Component } from 'react';
import styled from 'styled-components';

const NavigatorList = styled.ul`
    list-style: none;
    flex-wrap: wrap;
    align-items: center;
    display: flex;
    flex: 1 1 auto;
    justify-content: space-around;
    height: 100%;
    padding: 0;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    @media (max-width: 600px) {
        display: none;
    }
`;

const NavigatorItem = styled.li`
    text-align: center;
    transition: border-bottom .1s; 
    flex-direction: column;
    margin-bottom: 5px;
    justify-content: center;
    font-size: 18px;
    color: #f5f5f5;
    border-radius: 1px;
    border-bottom: ${(props) => props.selected ? 'solid 3px' : 'none'};
    :hover {
        border-bottom: solid 3px;
    }
`;

class Navigator extends Component {
    constructor(props) {
        super(props);
        this.props = props;
    }

    render() {
        const { links } = this.props;

        return (
            <NavigatorList>
               { 
                   links.map(({title, action, selected}) => 
                       <NavigatorItem selected={selected} onClick={action} >
                            {title}
                        </ NavigatorItem>
                   )
               }
            </ NavigatorList>
        );
    }
}

export default Navigator;


