import React, { Component } from 'react';
import styled from 'styled-components';

const Header = styled.div`
    margin-top: 50px;
    display: flex;
    flex: 1 1 auto;
    height: 100px;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-end;
`;

export default Header;



