import React, { Component } from 'react';
import styled from 'styled-components';

const contentSize = {
    small: 300,
    medium: 600,
    large: 900
};

const ContentBlock = styled.div`
    flex: 1 1 auto;
    min-width: ${(props) => `${contentSize[props.size]}px`};
    margin: 0px;
    color: #f5f5f5;
    font-size: 30px;
    text-align: center;
    line-height: 125px;
    vertical-align: middle;
    min-height: 100px;
    border-radius: 3px;
    @media (max-width: 950px) {
        min-width: ${(props) => `${contentSize[props.size] >= 900 ? 600 : contentSize[props.size]}px`};
    }
    @media (max-width: 650px) {
        min-width: ${(props) => `${contentSize[props.size] >= 600 ? 300 : contentSize[props.size]}px`};
    }
    @media (max-width: 350px) {
        min-width: none;
    }
`;

export default ContentBlock;


