import React, { Component } from 'react';
import Home from './views/Home';
import Contact from './views/Contact';
import About from './views/About';
import styled from 'styled-components';
import Header from './blocks/Header';
import logoSource from './logo.svg';
import Navigator from './blocks/Navigator';

const pages = {
    'home': <Home />,
    'contact': <Contact />,
    'about': <About />
};

const Title = styled.div`
    font-size: 35px;
    display: flex;
    flex: 2 2 auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    align-items: center;
    color: #f5f5f5;
`;

const Logo = styled.img`
    width: 40px;
    padding-left: 10px;
    padding-right: 10px;
`;

class App extends React.Component {
    state = {
        view: 'home'
    };

    renderCurrentState() {
        return pages[this.state.view];
    }

    changeView(newView) {
        this.setState({
            view: newView
        });
    }

    render() {
        const testLinks = [
            {
                title: 'Home',
                selected: (this.state.view === 'home'),
                action: () => this.changeView('home')
            },
            {
                title: 'About',
                selected: (this.state.view === 'about'),
                action: () => this.changeView('about')
            },
            {
                title: 'Contact us',
                selected: (this.state.view === 'contact'),
                action: () => this.changeView('contact')
            }
        ];
        return (
            <div>
                <Header>
                    <Title>
                        <Logo src={logoSource} />
                        Brianne Melissa Events
                    </Title>
                    <Navigator links={testLinks} />
                </Header>
                { this.renderCurrentState() }
            </div>
        );
    }
}

export default App;



